@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200&display=swap');
*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}
html{
  overflow-x: hidden;
  scroll-behavior: smooth;
}
.content{
  width: 100%;
  min-height: 100vh;
  background: #f1f1f1;
}
.header{
  width: 100%;
  height: 80px;
  position: fixed;
  box-shadow: 0 12px 10px rgba(0,0,0,.5);
  border-bottom-right-radius: 20%;
  border-bottom-left-radius: 20%;
  z-index: 10;

}
.header nav{
  width: 100%;
  height: 100%;
  background-color:#354046;
  display:flex;
  align-items: center;
  border-bottom-right-radius: 20%;
  border-bottom-left-radius: 20%;
  flex-direction: row;
  position: relative;
  z-index:10;
}
.header nav .burger{
  position:absolute;
  right: 6%;
  font-size:1.3rem;
  color:#f1f1f1;
  cursor: pointer;
  display: none;
}
.header nav .logo, .social_media, ul{
  width: 100%;
}
.header nav .logo img{
  width:100px;
  height:100px;
  object-fit: contain;
  margin:12px 0px 0px 15px;
}
.header nav .social_media, ul{
  display: flex;
  justify-content: center;
  gap:20px;
}
.header nav .social_media i{
  cursor: pointer;
  font-size:1.2rem;
  color:white;
  transition:ease-in-out .3s;
}
.header nav .social_media i:hover{
  color:#aaaaaa;
}
.header nav .social_media{
 position: absolute;
 top: 30%;
}
.header nav ul{
   display: flex;
   justify-content: flex-end;
}
.header nav ul li{
  margin-right: 25px;
  padding:4px 10px;
  text-align: center;
  border-radius: 5px;
  cursor: pointer;
}
.header nav ul li:hover, .active{
  background-color:#F0482B;
  transition:ease-in-out .3s;
}
.header nav ul li a{
  color:#d3d3d3;
  font-family: tahoma, arial;
  font-size:.9rem;
}

/* Section CSS */

.content .sections{
  width:100%;
  height: 100vh;
  background-color:#f1f1f1;
}
.content .sections section#home, section#products, section#about{
  width:100%;
  height: 100vh;
}
.content .sections section#home{
  width: 100%;
  height: 100vh;
  background-repeat: no-repeat;
  background-size:cover;
  background-position:center;
  display:flex;
  align-items: center;
  position: relative;
  padding:0px 5px 0px 5px;
}
.content .sections section#products{
  background-color:#f1f1f1;
}
.content .sections section#about{
  background-color:gray;
}
.content .sections section .overlay_box{
  width:100%;
  height:100%;
  position:absolute;
  background-color:rgba(0,0,0,.5);
}
.content .sections section .card{
  width:auto;
  height:auto;
  background-color:#e7e7e7;
  margin-left:10%;
  border:5px solid #354046;
  z-index: 3;
  display:flex;
  padding:0px 3% 5%;
  flex-direction:column;
  font-family: 'Poppins';
  margin-top:5%;
}
.content .sections section .card .title{
  display:flex;
  flex-direction: column;
  align-items: center;
}
.content .sections section .card img{
  width:150px;
  height:150px;
  object-fit: contain;
  margin:12px 0px 0px 0px;
}
.content .sections section h1{
   font-size:1.8rem;
   font-weight: 600;
   color:#F0482B;
   letter-spacing: .2rem;
   padding-bottom: 1px;
   border-bottom: 2px solid #354046;
   text-align: center;
}
.content .sections section span{
  font-size:1rem;
  text-align: center;
  margin-top:6px;
  color:#333636;
  font-family: tahoma, arial;
  font-style: italic;
  letter-spacing: .1rem;
}
.content .sections section .card .contact_information{
  display: flex;
  flex-direction: column;
  margin: 45px 0px 0px 10px;
  color:#333636;
  gap:15px;
}
.content .sections section .card .contact_information label{
  margin-left:10px;
  font-family: tahoma, arial;
  font-size:.9rem;
}

@media screen and (max-width:900px){

  /* Responsive Header CSS */

  .header nav{
    flex-direction: column;
    border-radius: 0;
  }
  .header nav ul{
    position: absolute;
    width: 100%;
    flex-direction: column;
    padding:0;
    background-color:#354046;
    border-bottom-right-radius: 20%;
    border-bottom-left-radius: 20%;
    top:100%;
    padding-bottom:35px;
    transition:ease-out .2s;
    box-shadow: 0 12px 10px rgba(0,0,0,.5);
  }
  .header nav ul.show{
    height:26vh;
    top:100%;
    
  }
  .header nav ul.hidden{
    height:0vh;
    top:0%;
    opacity: 0;
  }

  .header nav ul li{
    margin:0;
    border-radius: 0;
  }
  .header nav .burger{
    top:30%;
    display: block;
  }
  .header nav .logo img{
    margin:0px 0px 0px 5px;
  }

  /* Responsive Section CSS */

  .content .sections section#home{
      justify-content:center;
  }
  .content .sections section .card{
    margin:0;
  }
  .content .sections section .card{
    padding:0px 8% 12%;
  }
  
}





